import React, { useState, useEffect } from "react";
import {
  Container,
  Typography,
  Button,
  Divider,
  IconButton,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Dialog,
  DialogTitle,
  DialogContent,
  TextField,
  Snackbar,
  SnackbarContent,
} from "@material-ui/core";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { ArrowBack, Close, Add } from "@material-ui/icons";
import { red, green } from "@material-ui/core/colors";
import api from "../../api/api";
import { logout } from "../../actions";

const MOM = (props) => {
  const { logout, history } = props;
  const [formvalues, setformvalues] = useState({
    id: "",
    name: "",
    phone: "",
    email: "",
    address: "",
    district: "",
    role: "",
    contact_person: "",
  });

  const [wait, setwait] = useState(false);
  const [createdialog, setcreatedialog] = useState(false);
  const [editdialog, seteditdialog] = useState(false);
  const [toast, settoast] = useState(false);
  const [message, setmessage] = useState(null);
  const [status, setstatus] = useState(null);
  const [mom, setMOM] = useState([]);

  useEffect(() => {
    document.title = "MOM";
    fetchMOM();
  }, []);

  const fetchMOM = async () => {
    try {
      const response = await api.get(`/api/mom`);
      return setMOM(response.data.data);
    } catch (err) {
      return;
    }
  };

  const submit = async (e) => {
    e.preventDefault();
    if (localStorage.getItem("mom-admin")) {
      try {
        setwait(true);

        const response = await api.post(`/api/mom`, formvalues, {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("mom-admin"),
          },
        });

        setMOM([
          ...mom,
          {
            ...formvalues,
            id: response.data.id,
          },
        ]);
        setformvalues({
            id: "",
            name: "",
            phone: "",
            email: "",
            address: "",
            district: "",
            role: "",
            contact_person: "",
        });
        setstatus(green[700]);
        setmessage("Added successfully");
        setwait(false);
        setcreatedialog(false);
        return settoast(true);
      } catch (err) {
        setwait(false);
        setstatus(red[700]);
        setmessage("Something went wrong");
        return settoast(true);
      }
    } else {
      return logout();
    }
  };

  const submitEdit = async (e) => {
    e.preventDefault();
    if (localStorage.getItem("mom-admin")) {
      try {
        setwait(true);
        await api.patch(`/api/mom/${formvalues.id}`, formvalues, {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("mom-admin"),
          },
        });
        setMOM(mom.map((a) => (a.id === formvalues.id ? formvalues : a)));
        setformvalues({
            id: "",
            name: "",
            phone: "",
            email: "",
            address: "",
            district: "",
            role: "",
            contact_person: "",
        });
        seteditdialog(false);
        setstatus(green[700]);
        setmessage("Updated Successfully");
        setwait(false);
        return settoast(true);
      } catch (err) {
        setwait(false);
        setstatus(red[700]);
        setmessage("Something went wrong");
        return settoast(true);
      }
    } else {
      return logout();
    }
  };

  const deletesubmit = async () => {
    if (localStorage.getItem("mom-admin")) {
      try {
        setwait(true);
        await api.delete(
          `/api/mom/${formvalues.id}`,

          {
            headers: {
              Authorization: "Bearer " + localStorage.getItem("mom-admin"),
            },
          }
        );
        setMOM(mom.filter((a) => a.id !== formvalues.id));
        setformvalues({
            id: "",
            name: "",
            phone: "",
            email: "",
            address: "",
            district: "",
            role: "",
            contact_person: "",
        });
        seteditdialog(false);
        setstatus(green[700]);
        setmessage("Deleted Successfully");
        setwait(false);
        return settoast(true);
      } catch (err) {
        setwait(false);
        setstatus(red[700]);
        setmessage("Something went wrong");
        return settoast(true);
      }
    } else {
      return logout();
    }
  };

  const renderlist = () => {
    if (mom.length) {
      return (
        <Table size="small">
          <TableHead>
            <TableRow>
              <TableCell padding="checkbox">#</TableCell>
              <TableCell>Name</TableCell>
              <TableCell>Contact Person</TableCell>
              <TableCell>Phone</TableCell>
              <TableCell>District</TableCell>
              <TableCell>Role</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {mom.map((a, index) => {
              return (
                <TableRow
                  hover
                  key={index}
                  style={{ cursor: "pointer" }}
                  onClick={() => {
                    setformvalues({ ...a });
                    seteditdialog(true);
                  }}
                >
                  <TableCell>
                    <Typography>{index+1}</Typography>
                  </TableCell>
                  <TableCell>
                    <Typography>{a.name}</Typography>
                  </TableCell>
                  <TableCell>
                    <Typography>{a.contact_person}</Typography>
                  </TableCell>
                  <TableCell>
                    <Typography>{a.phone}</Typography>
                  </TableCell>
                  <TableCell>
                    <Typography>{a.district}</Typography>
                  </TableCell>
                  <TableCell>
                    <Typography>{a.role}</Typography>
                  </TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      );
    } else {
      return (
        <Typography align="center" variant="h6" style={{ marginTop: 300 }}>
          NO DATA TO DISPLAY!
        </Typography>
      );
    }
  };

  const renderdialog = () => (
    <Dialog
      fullWidth
      maxWidth="xs"
      open={createdialog}
      onClose={() => {
        setformvalues({
            id: "",
            name: "",
            phone: "",
            email: "",
            address: "",
            district: "",
            role: "",
            contact_person: "",
        });
        setcreatedialog(false);
      }}
    >
      <DialogTitle>Add MOM</DialogTitle>
      <DialogContent dividers>
        <form onSubmit={submit}>
        <TextField
            required
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            label="Name/Organisation Name"
            value={formvalues.name}
            onChange={(e) =>
              setformvalues({ ...formvalues, name: e.target.value })
            }
          />
          <TextField
            
            fullWidth
            variant="outlined"
            margin="normal"
            
            size="small"
            label="Contact Person"
            value={formvalues.contact_person}
            onChange={(e) =>
              setformvalues({ ...formvalues, contact_person: e.target.value })
            }
          />
          <TextField
            required
            fullWidth
            variant="outlined"
            margin="normal"
            type="number"
            size="small"
            label="Phone"
            value={formvalues.phone}
            onChange={(e) =>
              setformvalues({ ...formvalues, phone: e.target.value })
            }
          />
          <TextField
            required
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            type="email"
            label="Email"
            value={formvalues.email}
            onChange={(e) =>
              setformvalues({ ...formvalues, email: e.target.value })
            }
          />
           <TextField
            required
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            label="Address"
            value={formvalues.address}
            onChange={(e) =>
              setformvalues({ ...formvalues, address: e.target.value })
            }
          />
           <TextField
           
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            label="District"
            value={formvalues.district}
            onChange={(e) =>
              setformvalues({ ...formvalues, district: e.target.value })
            }
          />
           <TextField
            required
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            label="Role (Eg: FPO, FPC, FIG, Service Provider)"
            value={formvalues.role}
            onChange={(e) =>
              setformvalues({ ...formvalues, role: e.target.value })
            }
          />

          <div
            style={{
              display: "flex",
              justifyContent: "flex-end",
              marginTop: 10,
            }}
          >
            <Button
              disabled={wait}
              type="submit"
              variant="contained"
              color="primary"
              style={{ marginRight: 10 }}
            >
              Submit
            </Button>
            <Button
              variant="contained"
              onClick={() => {
                setformvalues({
                    id: "",
                    name: "",
                    phone: "",
                    email: "",
                    address: "",
                    district: "",
                    role: "",
                    contact_person: ""
                });
                setcreatedialog(false);
              }}
            >
              Cancel
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );

  const rendereditdialog = () => (
    <Dialog
      fullWidth
      maxWidth="xs"
      open={editdialog}
      onClose={() => {
        setformvalues({
            id: "",
            name: "",
            phone: "",
            email: "",
            address: "",
            district: "",
            role: "",
            contact_person: "",
        });
        seteditdialog(false);
      }}
    >
      <DialogTitle>Edit MOM</DialogTitle>
      <DialogContent dividers>
        <form onSubmit={submitEdit}>
        <TextField
            required
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            label="Name/Organisation Name"
            value={formvalues.name}
            onChange={(e) =>
              setformvalues({ ...formvalues, name: e.target.value })
            }
          />
          <TextField
            
            fullWidth
            variant="outlined"
            margin="normal"
            
            size="small"
            label="Contact Person"
            value={formvalues.contact_person}
            onChange={(e) =>
              setformvalues({ ...formvalues, contact_person: e.target.value })
            }
          />
          <TextField
            required
            fullWidth
            variant="outlined"
            margin="normal"
            type="number"
            size="small"
            label="Phone"
            value={formvalues.phone}
            onChange={(e) =>
              setformvalues({ ...formvalues, phone: e.target.value })
            }
          />
          <TextField
            required
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            type="email"
            label="Email"
            value={formvalues.email}
            onChange={(e) =>
              setformvalues({ ...formvalues, email: e.target.value })
            }
          />
           <TextField
            required
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            label="Address"
            value={formvalues.address}
            onChange={(e) =>
              setformvalues({ ...formvalues, address: e.target.value })
            }
          />
           <TextField
           
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            label="District"
            value={formvalues.district}
            onChange={(e) =>
              setformvalues({ ...formvalues, district: e.target.value })
            }
          />
           <TextField
            required
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            label="Role (Eg: FPO, FPC, FIG, Service Provider)"
            value={formvalues.role}
            onChange={(e) =>
              setformvalues({ ...formvalues, role: e.target.value })
            }
          />
          <div
            style={{
              display: "flex",
              justifyContent: "flex-end",
              marginTop: 10,
            }}
          >
            <Button
              disabled={wait}
              type="submit"
              variant="contained"
              color="primary"
              style={{ marginRight: 10 }}
            >
              Update
            </Button>
            <Button
              disabled={wait}
              style={{ marginRight: 10 }}
              color="secondary"
              variant="contained"
              onClick={() => {
                if (
                  window.confirm(
                    `Are you sure you want to delete? You cannot recover once deleted!`
                  )
                ) {
                  return deletesubmit();
                }
              }}
            >
              Delete
            </Button>

            <Button
              variant="contained"
              onClick={() => {
                setformvalues({
                    id: "",
                    name: "",
                    phone: "",
                    email: "",
                    address: "",
                    district: "",
                    role: "",
                    contact_person: "",
                });
                seteditdialog(false);
              }}
            >
              Cancel
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );

  return (
    <Container>
      <Typography variant="h6">
        <IconButton
          edge="start"
          onClick={() => {
            history.goBack();
          }}
        >
          <ArrowBack />
        </IconButton>
        MOM Members Management
      </Typography>
      <div style={{ display: "block", marginTop: 8 }}>
        <Button
          variant="contained"
          color="secondary"
          startIcon={<Add />}
          onClick={() => setcreatedialog(true)}
        >
          Add New
        </Button>
      </div>
      <Divider style={{ marginTop: 20 }} />
      {renderlist()}
      {renderdialog()}
      {rendereditdialog()}

      <Snackbar
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
        open={toast}
        autoHideDuration={3000}
        onClose={() => {
          settoast(false);
        }}
      >
        <SnackbarContent
          style={{ backgroundColor: status }}
          message={message}
          onClose={() => {
            settoast(false);
          }}
          action={
            <IconButton
              key="close"
              color="inherit"
              onClick={() => settoast(false)}
            >
              <Close />
            </IconButton>
          }
        ></SnackbarContent>
      </Snackbar>
    </Container>
  );
};

export default connect(null, { logout })(withRouter(MOM));
